import Head from "next/head";
import Navbar from "../components/Navbar";


const About = () => (
    <div>
        <Navbar />
        <Head>
            <title>Gerilimsiz Todo Application</title>
            <meta name="viewport" content="initial-scale=1.0, width=device-width" />
            <link href="/static/App.css" rel="stylesheet" />
        </Head>
        <layout>
            <h1  className="header">Gerilimsiz TodoApp</h1>
            <p style={navStyle}>Gerilimsiz is the stress reduction App that would help you schedule and       manage your workflow by adding and deleting tasks todo, thereby reducing workload stress
            </p>
            <h3 style={linkStyle}>About App</h3>
      </layout>
    </div> 
);

const linkStyle = {
    textDecoration: 'none',
    weight: 'bold',
    color: '#000',
    marginTop: '40px',
}

const navStyle = {
    margiTop: '10px',
    fontStyle: 'italic',  
    textAlign: 'center',
    padding: '40px',
    color: '#000'
}

export default About;